@use 'mixins/hover';
@use 'mixins/shadow-dom/component';

@mixin set-border($composition) {
  @if $composition {
    border-#{$composition}: var(--theme-app-header--border-width)
      solid
      var(--ix-pane--border-color);
  }
}

:host(.floating-pane) {
  --ix-pane--background: var(--theme-pane-floating--background);
  --ix-pane--border-color: var(--theme-pane-floating--border-color);
  --ix-pane--color: var(--theme-pane-floating--color);
  --ix-pane--box-shadow: var(--theme-pane-floating--box-shadow);
}

:host(.inline-pane) {
  --ix-pane--background: var(--theme-pane-inline--background);
  --ix-pane--border-color: var(--theme-pane-inline--border-color);
  --ix-pane--color: var(--theme-pane-inline--color);
  --ix-pane--box-shadow: var(--theme-pane-inline--box-shadow);
}

:host {
  display: flex;
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
  box-shadow: var(--ix-pane--box-shadow);
  background-color: var(--ix-pane--background);

  @include component.ix-component;

  aside {
    width: 100%;
    height: 100%;
  }

  .pane-icon {
    color: var(--ix-pane--color);
  }

  .slot-header-content {
    display: inline-block;
    position: relative;
    max-height: 2rem;
  }

  .title-text {
    display: flex;
    align-items: center;
    gap: var(--theme-space-1);
    overflow: hidden;

    .title-text-overflow {
      width: 100%;
      height: 100%;
      display: contents;

      * {
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
      }
    }
  }

  .header-gap {
    gap: var(--theme-space-1);
  }

  .top-bottom-pane {
    display: flex;
    flex-direction: column;
    flex-grow: 1;

    .title {
      display: flex;
      flex-direction: row;
      align-items: center;
      margin: var(--theme-space--1) var(--theme-space-2) var(--theme-space--1)
        var(--theme-space-2);
    }

    .title-finished {
      padding: 0 !important;
      margin: var(--theme-space-2) var(--theme-space-2) var(--theme-space-2)
        var(--theme-space-2);
    }

    .title-expanded {
      display: flex;
      flex-direction: row;
    }

    .title-hide-on-collapse {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
      padding: 0 !important;
      margin: var(--theme-space-2) var(--theme-space-2) var(--theme-space-2)
        var(--theme-space-2);
    }
  }

  .left-right-pane {
    display: flex;
    flex-grow: 1;

    .title {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: var(--theme-space-2) var(--theme-space--1) var(--theme-space-2)
        var(--theme-space--1);

      .title-text {
        flex-direction: row-reverse;
        justify-content: start;
        flex-direction: row;
      }

      .rotate {
        writing-mode: vertical-lr;
        text-orientation: mixed;
        transform: rotate(180deg);
        flex-direction: row-reverse;

        ix-icon {
          transform: rotate(90deg);
        }
      }
    }

    .title-finished {
      padding: 0 !important;
      margin: var(--theme-space-2) var(--theme-space-2) var(--theme-space-2)
        var(--theme-space-2);
    }

    .title-expanded {
      display: flex;
      flex-direction: row;
    }

    .title-hide-on-collapse {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
      padding: 0 !important;
      margin: var(--theme-space-2) var(--theme-space-2) var(--theme-space-2)
        var(--theme-space-2);
    }
  }

  .left-right-pane.expanded {
    flex-direction: column;
  }

  .mobile-pane {
    display: flex;
    flex-direction: column;
    flex-grow: 1;

    .title-mobile {
      display: flex;
      flex-direction: row-reverse;
      justify-content: space-between;
      margin: var(--theme-space-1) var(--theme-space-2);
    }
  }

  .side-pane-content {
    padding: var(--theme-space-1) var(--theme-space-2) var(--theme-space-3)
      var(--theme-space-2);
    overflow: auto;
    height: 100%;
    width: 100%;
  }

  .slot-header {
    max-height: 2rem;
    max-width: unset;
  }

  .slot-header.rotate {
    max-height: unset;
    max-width: 2rem;
  }
}

:host(.not-visible) {
  display: none;
}

:host(.nav-left-border) {
  border-left: var(--theme-app-header--border-width) solid
    var(--ix-pane--border-color);
}

:host(.left-pane-border) {
  @include set-border(right);
}

:host(.right-pane-border) {
  @include set-border(left);
}

:host(.top-pane-border) {
  @include set-border(bottom);
}

:host(.bottom-pane-border) {
  @include set-border(top);
}

:host(.mobile-overlay) {
  width: 100%;
  position: absolute;
  z-index: 100 !important;

  .side-pane-content {
    height: calc(100% - 2.5rem);
  }
}

:host(.mobile-border-top) {
  @include set-border(bottom);
}

:host(.mobile-border-bottom) {
  @include set-border(top);
}

:host(.top-expanded) {
  top: 0;
}

:host(.bottom-expanded) {
  bottom: 0;
}

:host(.left-right-pane) {
  height: 100%;
}

:host(.top-bottom-pane) {
  width: 100%;
}
